import React from 'react';
import './ProteineBody.css'

import bowl_quinoa from '../../assets/ImageVeganPage/bowl_quinoa.webp';
import curry from '../../assets/ImageVeganPage/curry.png';
import nouilles from '../../assets/ImageVeganPage/nouilles.webp';
import Padthai from '../../assets/ImageVeganPage/Padthai.jpg';


import clock  from '../../assets/ImageHomePage/clock-one.png';
import celeri from '../../assets/ImageDetoxPage/celeri.png';
// import tofu  from '../../assets/ImageHomePage/tofu.png';
import idea from '../../assets/ImageDetoxPage/idea.png';


import oeuf from '../../assets/ImageNoglutenPage/oeuf.png';
import douce from '../../assets/ImageNoglutenPage/douce.png';
import amande from '../../assets/ImageNoglutenPage/amande.png';

const ProteineBody = () => {
  return (
    <div className='detox'>



    <div className='tips-detox'>
        <img className='idea' src={idea} alt="" />
        <div className='tips-ingredient'>
        <img src={oeuf} alt="" />
        <p>"Riches en protines de haute qualit, vitamines essentielles et minraux, soutiennent la sant musculaire, crbrale et apportent une nergie durable."</p>
        </div>
       
        <div className='tips-ingredient'>
        <img src={douce} alt="" />
        <p>"Riches en fibres, vitamines A et C, antioxydants et minraux, favorisent la digestion, renforcent le systme immunitaire et apportent une nergie durable."</p>
        </div>

        <div className='tips-ingredient'>
        <img src={amande} alt="" />
        <p>"Riches en bonnes graisses, protines, fibres, vitamines E et minraux, boostent l'nergie, protgent le cur et favorisent une belle peau et des os solides."</p>
        </div>




      
    </div>

    


  <div className='detox-container'>

    <div className='titre-detox'>
    <h1>Recettes Protines</h1>
    </div>

    <div className='detox-product'>
    <img src={bowl_quinoa} alt="" />


    <div className='detail-detox-product'>
    <h2>Bowl de quinoa et lgumes d'hiver</h2>
    <p className='p-detox'>Un classique revisit sans gluten, avec du riz parfum, des lgumes croquants, des ufs moelleux et une touche dexotisme.</p>
    <div className='detail-detox'>
    <p>Plat - trs facile</p>
    <img src={clock} alt="" />
    <p>15 min</p>
    </div>
    </div>

    
    </div>

    <div className='detox-product'>
    <img src={curry} alt="" />


    <div className='detail-detox-product'>
    <h2>Curry de lgumes (vgtalien)</h2>
    <p className='p-detox'>Des cookies dors et croustillants  l'extrieur, fondants  l'intrieur, parfaits pour un plaisir gourmand et sans compromis.</p>
    <div className='detail-detox'>
    <p>Biscuit - trs facile</p>
    <img src={clock} alt="" />
    <p>30 min</p>
    </div>
    </div>

    
    </div>

    <div className='detox-product'>
    <img src={nouilles} alt="" />


    <div className='detail-detox-product'>
    <h2>Nouilles aux lgumes</h2>
    <p className='p-detox'>Un gratin gnreux, alliant la douceur des patates douces et une texture fondante, idal pour un repas rconfortant.</p>
    <div className='detail-detox'>
    <p>Plat - trs facile</p>
    <img src={clock} alt="" />
    <p>1 heures 40 minutes</p>
    </div>
    </div>

    
    </div>
    
    <div className='detox-product'>
    <img src={Padthai} alt="" />


    <div className='detail-detox-product'>
        <h2>Pad Thai</h2>
    <p className='p-detox'>Un dessert raffin et sans gluten, aux saveurs intenses de rhum et damandes, pour une pause sucre incontournable.</p>
    <div className='detail-detox'>
    <p>Dessert - trs facile</p>
    <img src={clock} alt="" />
    <p>15 min</p>
    </div>
    </div>

    
    </div>



    

   

  </div>
</div>
  )
}

export default ProteineBody;
